.header {
    height: 70px;
    background-color: #12151a;
    display: flex;
    justify-content: center;
    width: 100%;
    z-index: 100;
    border-bottom: 2px solid #fff;
    position: fixed;
}

.container {
    width: 80%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

